define( '../src/sublayouts/m20-notification/m20-notification',[ 'jquery' ],
	function ( $, utils ) {

		'use strict';

		var Notification = function ( $component ) {

			this.$component = $component;
		};

		Notification.prototype.init = function () {

			var _this = this;

			_this.$time = this.$component.find('[data-time]');
			var time = this.$component.data('timeout')
			var repeat = this.$component.data('time-repeat')
			this.$closenew = this.$component.find('.m20-new-close');

			function notifictionShow(){
				var x = readCookie('notification')
				var stay = time * 1000
				_this.$component.addClass('shown')	
				setTimeout(notifictionHide, stay)
				createCookie('notification','1',repeat);
			}

			function createCookie(name,value,hours) {
				//console.log(hours * 60 * 60 * 1000)
    			var expires = "";
    				if (hours) {
       					 var date = new Date();
       					 date.setTime(date.getTime() + (hours * 60 * 1000) );
       				
        				 expires = "; expires=" + date.toUTCString();
			   		}
			    document.cookie = name + "=" + value + expires + "; path=/";
			}

			function readCookie(name) {
			    var nameEQ = name + "=";
			    var ca = document.cookie.split(';');
			    for(var i=0;i < ca.length;i++) {
			        var c = ca[i];
			        while (c.charAt(0)==' ') c = c.substring(1,c.length);
			        if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
			    }
			    return null;
			}

			function notifictionHide(){
				if (_this.$component.hasClass('shown')){
						_this.$component.removeClass('shown')	
				}
			}

			//console.log(readCookie('notification'))

			var x = readCookie('notification')
			function thisNew(){
				var x = readCookie('notification')
				//console.log('intervel time - ' + x)

				if (x === null) {
					notifictionShow()
				}
			}
			
			thisNew();
			setInterval(thisNew,60000)
			

			this.$closenew.on('click', function(){	
				//console.log('click')
				_this.$component.removeClass('shown')
			});

			this.$close = this.$component.find('.m20-notification__dismiss');

			this.$close.one('click', function(ev){
				ev.preventDefault();
				_this.$component.fadeOut(function(){
					_this.$component.remove();
				});
			});

			};
		

		

		return Notification;

	});

