define( '../src/sublayouts/m68-message-item/m68-message-item',[ 'jquery' ], function ( $ ) {

	'use strict';

	var M68_Message = function ( $component ) {

		this.$component = $component;

		return this;
	};

	M68_Message.prototype.init = function () {
		var _this = this;

		this.bindModal();
	};

	M68_Message.prototype.bindModal = function() {
        var _this = this,
            $parent = $('.m68-message-item--mail_modal'),
            $trigger = $parent.find('.m39-modal__trigger'),
            $confirm = $parent.find('.commentssubmit'),
            $close = $parent.find('.m39-modal__button--close');

        $('.m68-message-item--reply').off('click.modal').on('click.modal', function() {
            if (!$(this).hasClass('disabled')) {
                $trigger.trigger('click');
                $confirm.attr('data-relayaction', 'reply');
            };
        });

        $('.m68-message-item--forward').off('click.modal').on('click.modal', function() {
            if (!$(this).hasClass('disabled')) {
                $trigger.trigger('click');
                $confirm.attr('data-relayaction', 'forward');
            };
        });

        $('.m68-message-item--mail_cancel').off('click.modal').on('click.modal', function() {
            $close.trigger('click');
        });

        $parent.find('.m39-modal__button--close').off('click.close').on('click.close', function() {
            $parent.find('.select2-selection__rendered').find('.select2-selection__choice').each(function() {
                $(this).remove();
            });
            $parent.find('input, textarea').each(function() {
                $(this).parsley().reset();
                $(this).val('');
                $(this).removeClass('form-field__input--error').removeClass('form-field__input--validated');
                $(this).closest('span').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
            });
            $parent.find('select').each(function() {
                if ($(this).hasClass('form-field__input--select2')) {
                	$(this).val(null).trigger("change");
                };
                $(this).parsley().reset();
                $(this).closest('span').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
            });
        });
    };

	return M68_Message;

} );

