define('../src/journeys/j1.0-corporate-partnership/j1.0-corporate-partnership',['jquery', 'parsley'], function($, parsley) {

    'use strict';

    var j1_CP = function($journey) {

        this.$journey = $journey;

        return this;
    };

    j1_CP.prototype.init = function() {
        var _this = this;

        this.uploadConfirmButton = $('.j1-CP--user--add__parent').find('.form-field__uploader-details').find('.button--small').html();

        ////////////////////////////
        //START INPUT OF GOOGLEMAP//
        ////////////////////////////
        $('.form-field__input--map').each(function(){
            $(this).off('focusout.cp').on('focusout.cp', function() {
                if ( $(this).parsley().validate() == true ) {
                    if ($(this).val().includes('goo.gl')) {
                        _this.shorten($(this).val());
                    } else {
                        _this.deploy($(this).val());
                    };
                };
            });
        });
        //////////////////////////
        //END INPUT OF GOOGLEMAP//
        //////////////////////////

        //////////////////
        //START ADD USER//
        //////////////////
        $('.j1-CP--user--add__button').off('click').on('click', function() {
            _this.callAddModal($(this));
        });
        $('.j1-CP--user--edit__button').off('click').on('click', function() {
            _this.callAddModal($(this));
        });
        $('.j1-CP--user--add__close').off('click').on('click', function() {
            $(this).closest('.m39-modal__dialog').find('.m39-modal__button--close').trigger('click')
        });
        $('.j1-CP--user--remove__button').off('click').on('click', function() {
            $(this).closest('.j1-CP--user__wrapper').fadeOut(100).remove();
        });
        ////////////////
        //END ADD USER//
        ////////////////
    };

    ////////////////////
    //METHODS ADD USER//
    ////////////////////
    j1_CP.prototype.callAddModal = function($button) {
        var _this = this,
            $title = $button.attr('data-title'),
            $confirmButton = $button.attr('data-confirmButton'),
            $parent = $('.j1-CP--user--add__parent');

        if ( $button.hasClass('j1-CP--user--edit__button') ){
            var $bParent = $button.closest('.j1-CP--user__details'),
                $name = $bParent.find('.j1-CP--user__name').html(),
                $position = $bParent.find('.j1-CP--user__position').html(),
                $role = $bParent.find('.j1-CP--user__type').html(),
                $mobile = $bParent.find('.j1-CP--user__mobile').find('span').html(),
                $tel = $bParent.find('.j1-CP--user__tel').find('span').html(),
                $email = $bParent.find('.j1-CP--user__email').find('span').html(),
                $address = $bParent.find('.j1-CP--user__address').html(),
                $image = $bParent.prev().attr('data-src');

            $parent.find('input[name=fullname]').val($name);
            $parent.find('input[name=designation]').val($position);
            $parent.find('input[name=mobile]').val($mobile.slice(-9));
            $parent.find('input[name=landline_user]').val($tel);
            $parent.find('input[name=email_user]').val($email);
            $parent.find('input[name=address]').val($address);
            $parent.find('select[name=user_role]').find('option').each(function(){
                $(this).attr('selected', false);
                if ( $role != undefined || $role != null ) {
                    if ( $(this).html().toLowerCase() == $role.toLowerCase()) {
                        $(this).attr('selected', 'selected');
                    }
                } else {
                    $parent.find('select[name=user_role]').find('option:first-child').attr('selected', 'selected');
                };
            });
            $parent.find('.form-field__preview').attr('src', $image);

            $parent.find('.button--remove').off('click.add').on('click.add', function() {
                $parent.find('.form-field__uploader-details').find('input').attr('required', true);
            });
            $parent.find('.form-field__uploader-details').find('input').attr('required', false);
            $parent.find('.form-field__uploader-details').find('.button--small').html($confirmButton);
        };

        $parent.find('.m39-modal__title').html($title);
        $parent.find('button[type=submit]').html($confirmButton);
        $('.j1-CP--user--add__trigger').trigger('click');

        $parent.find('.m39-modal__button--close').off('click.close').on('click.close', function() {
            $parent.find('input').each(function(){
                $(this).parsley().reset();
                $(this).val('');
                $(this).closest('span').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
            });
            $parent.find('.form-field__preview').attr('src', '../../images/preview@2x.png');
            $parent.find('.form-field__uploader-details').find('input').attr('required', true);
            $parent.find('.form-field__uploader-details').find('.button--small').html(_this.uploadConfirmButton);
            $parent.find('select[name=user_role]').find('option').each(function(){
                $(this).attr('selected', false);
            });
            $parent.find('select[name=user_role]').find('option:first-child').attr('selected', 'selected');

        });
    };
    ////////////////
    //END ADD USER//
    ////////////////

    //////////////////////////////
    //METHODS INPUT OF GOOGLEMAP//
    //////////////////////////////
    j1_CP.prototype.shorten = function(shortUrl) {
        var _this = this;

        jQuery.ajax({
            url: 'https://www.googleapis.com/urlshortener/v1/url?shortUrl='+shortUrl+'&key=AIzaSyBnQgg6k8SJ2Q4xeX6YjjeX7DtKF2BZKDw',
            dataType: 'json',
            error: function(jqXHR, textStatus, errorThrown) {
                console.log(errorThrown);
            },
            success: function(data) {
                _this.deploy(data.longUrl);
            }
        });
    };

    j1_CP.prototype.deploy = function($url) {
        var _this = this,
            $array = $url.split('/'),
            $address = $array[$array.indexOf('place')+1];

        if ( $array.indexOf('place') != -1 ) {
            jQuery.ajax({
                url: 'https://www.google.com/maps/embed/v1/place?key=AIzaSyBnQgg6k8SJ2Q4xeX6YjjeX7DtKF2BZKDw&q='+$address,
                dataType: 'html',
                beforeSend: function() {
                    if ($('.j1-CP--inputMap').hasClass('j1-CP--inputMap--active')) {
                        $('.form-field--inputMap').find('.j1-CP--inputMap--loader').show();
                    };
                },
                error: function(jqXHR, textStatus, errorThrown) {
                    console.log(errorThrown);
                },
                success: function(data) {
                    $('.j1-CP--inputMap').attr('src', 'https://www.google.com/maps/embed/v1/place?key=AIzaSyBnQgg6k8SJ2Q4xeX6YjjeX7DtKF2BZKDw&q='+$address);
                    $('.j1-CP--inputMap').off('load').on('load', function() {
                        $('.form-field--inputMap').find('.j1-CP--inputMap--loader').hide();
                        $('.j1-CP--inputMap').removeClass('j1-CP--inputMap--active').addClass('j1-CP--inputMap--active');
                    });
                }
            });
        } else {
            $('.j1-CP--inputMap').removeClass('j1-CP--inputMap--active');
            window.alert('Wrong Google Map link');
        };
    };
    //////////////////////////
    //END INPUT OF GOOGLEMAP//
    //////////////////////////

    return j1_CP;
});

