define('../src/sublayouts/m64-inbox/m64-inbox',['jquery'], function ($) {

	'use strict';

	var M64_INBOX = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M64_INBOX.prototype.init = function () {
		var _this= this;

		this.checkActive();

		$('.m64-inbox--tab_item').on('click', function() {
			if ( !($(this).hasClass('m64-inbox--tab_active')) ) {
				$('.m64-inbox--tab_item').each(function() {
					$(this).removeClass('m64-inbox--tab_active');
				});
				$(this).addClass('m64-inbox--tab_active');
				_this.checkActive();	
			};
		});

	};

	M64_INBOX.prototype.checkActive = function () {
		var _this= this;

		this.$component.find('.m64-inbox--tab_item').each(function() {
			var __this = this;

			if ( $(this).hasClass('m64-inbox--tab_active') ) {
				_this.$component.find('.m64-inbox--content').each(function(){
					$(this).fadeOut(500);
					$(this).hide();
					if ( $(__this).attr('data-control') == $(this).attr('data-controlled') ) {
						$(this).fadeIn(500);
						$(this).show();
					}
				});
			};
		});
	};

	return M64_INBOX;
});

