define('../src/sublayouts/m28-happiness-results/m28-happiness-results',['jquery', 'chart-config'], function($) {

    'use strict';

    var happinessResults = function($component) {

        this.$component = $component;

        return this;
    };

    happinessResults.prototype.init = function() {

        this.chartData = this.$component.data('chart-data');
        this.chartAxes = this.$component.data('axes-data');
        this.chartLegendX = this.$component.data('axes-legend-x');
        this.chartLegendY = this.$component.data('axes-legend-y');

        this.plotChart();
        //this.plotChartUpdateClone();
    };

    happinessResults.prototype.plotChart = function() {
        var _this = this,
            alignment;

        if ($('html').attr('dir') == "rtl") {
            alignment = 'right'
        } else {
            alignment = 'left'
        }

        this.$chartContainer = this.$component.find('[data-chart-container]');

        this.$chartContainer.highcharts({
            chart: {
                type: 'column',
                height: 300
            },
            title: {
                text: undefined
            },
            xAxis: {
                categories: _this.chartAxes,
                labels: {
                    style: {
                        fontSize: '16px'
                    }
                },
                crosshair: true,
                title: {
                    enabled: true,
                    useHTML: true,
                    style: {
                        fontWeight: 'bold',
                        fontSize: '16px'
                    },
                    text: _this.chartLegendX
                }
            },
            legend: {
                verticalAlign: 'top',
                align: alignment,
                itemStyle: {
                    fontSize: '16px'
                },
                rtl: $('html').attr('dir') == "rtl",
                symbolWidth: 16,
                symbolRadius: 10,
            },
            yAxis: {
                labels: {
                    formatter: function() {
                        return this.value + ' %';
                    },
                    style: {
                        fontSize: '16px'
                    }
                },
                min: 0,
                max: 100,
                tickInterval: 25,
                fontSize: '16px',
                title: {
                    enabled: true,
                    useHTML: true,
                    style: {
                        fontWeight: 'bold',
                        fontSize: '16px'
                    },
                    text: _this.chartLegendY
                }
            },
            tooltip: {
                //enabled: false,
                headerFormat: '<span style="font-size:16px;font-weight:500;direction: ' + $('html').attr('dir') + ';text-align: ' + alignment + ';">{point.key}</span><table style="direction: ' + $('html').attr('dir') + ';text-align: ' + alignment + ';">',
                pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
                    '<td style="padding:0"><b>{point.y:.1f} %</b></td></tr>',
                footerFormat: '</table>',
                shared: true,
                style: {
                    fontSize: '16px'
                },
                borderColor: 'transparent',
                useHTML: true
            },
            plotOptions: {
                column: {
                    pointPadding: 0.2,
                    borderWidth: 1,
                    groupPadding: 0.3
                }
            },
            series: _this.chartData
        });

    };

    happinessResults.prototype.plotChartUpdateClone = function() {
        window.setTimeout(function() {
            dewaGlobal.updateDashboardChart();
        }, 1100);
    };
    return happinessResults;

});

