define( '../src/sublayouts/m23-table-joint-services/m23-table-joint-services',[ 'jquery', 'chart-config' ],
	function ( $ ) {

		'use strict';

		var Table = function ( $component ) {

			this.$component = $component;
		};

		Table.prototype.init = function () {
			var _this = this;

			this.$component.find('.m23-table-joint-services--count_catch').html(this.$component.find('tbody .m23-table-joint-services--tr').length);

			this.initPie();
		};
		
		Table.prototype.initPie = function () {
			var _this = this;

			$('.m23-table-joint-services--tcell_data-container').each(function(){
				var data = parseFloat($(this).attr('data-rate')),
					color;

				if ( $(this).attr('data-status') == "green" ) {
					color = '#34B233';
				} else if ( $(this).attr('data-status') == "red" ) {
					color = '#D0021B';
				} else {
					color = '#F5A623';
				};

				$(this).highcharts({
					chart:{
						type: 'pie',
						width: 70,
						height: 70
					},
		            title: {
		            	text: undefined
		            },
				    plotOptions: {
				        pie: {
				            dataLabels: {
				                enabled: false
				            }
				        }
				    },
				    tooltip: {
				        enabled: false,
				        animation: false
				    },
		            series: [
		            	{
			            	name: null,
			            	color: color,
			            	data: [
			            		{
					            	name: undefined,
					            	color: color,
					            	y: data
				            	},
				            	{
				            		name: undefined,
					            	color: '#EAEAEA',
					            	y: 100-data
				            	}
			            	],
			            	states: {
			            		hover: {
			            			enabled: false
			            		}
			            	}
		            	}
		            ]
				});
			});
		};

		return Table;

	} );

