define('../src/sublayouts/m4-teaser/m4-teaser',['jquery'], function ($) {

	'use strict';

	var M4_TEASER = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M4_TEASER.prototype.init = function () {
		var _this= this;

		this.$component.closest('.grid__row').find('.m4-teaser--count_catch').html(this.$component.closest('.grid__row').find('.m4-teaser').length);

	};

	return M4_TEASER;
});

