define('../src/sublayouts/m65-inbox-list/m65-inbox-list',['jquery'], function ($) {

	'use strict';

	var M65_INBOXLIST = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M65_INBOXLIST.prototype.init = function () {
		var _this= this;

		this.checkActive();

		$('.m65-inbox-list--tab_item').on('click', function() {
			if ( !($(this).hasClass('m65-inbox-list--tab_active')) ) {
				$('.m65-inbox-list--tab_item').each(function() {
					$(this).removeClass('m65-inbox-list--tab_active');
				});
				$(this).addClass('m65-inbox-list--tab_active');
				_this.checkActive();
			};
		});

	};

	M65_INBOXLIST.prototype.checkActive = function () {
		var _this= this;

		this.$component.find('.m65-inbox-list--tab_item').each(function() {
			var __this = this;

			if ( $(this).hasClass('m65-inbox-list--tab_active') ) {
				_this.$component.find('.m65-inbox-list--content').each(function(){
					$(this).fadeOut(500);
					$(this).hide();
					if ( $(__this).attr('data-control') == $(this).attr('data-controlled') ) {
						$(this).fadeIn(500);
						$(this).show();
					}
				});
			};
		});
	};

	return M65_INBOXLIST;
});

