define('../src/sublayouts/m18-teaser/m18-teaser',['jquery'], function ($) {

	'use strict';

	var M18_TEASER = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M18_TEASER.prototype.init = function () {
		var _this= this;
		
		this.$component.find('.m18-teaser--count_catch').html(this.$component.find('.m18-teaser--item').length);

	};

	return M18_TEASER;
});

