define('../src/sublayouts/m1-hero/m1-hero',['jquery', 'slick', 'lib/utils'], function ($, slick, utils) {

	'use strict';

	var M1_HERO = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M1_HERO.prototype.init = function () {
		this.$M1_slider = $('.m1-hero__carousel');
		var $nav = $('.m1-hero__carousel-nav-inner'),
			$prev = $('.m1-hero__carousel-button--prev'),
			$next = $('.m1-hero__carousel-button--next'),
			$body = $('body'),
			$margin = 27;

		if ($('.m1-hero__carousel-slide').length > 1) {
			var that = this;

			// listen for carousel initialisation
			this.$M1_slider.on('init', function(event){
				// resize nav arrow container to fit around dot navigation
				/* $nav.css({
					width: ($('.slick-dots').width() + $prev.width() + $next.width() + ($margin*2))
				});*/
				// show pause/play button
				// that.$component.find('.m1-hero__carousel-button-play-pause').show();
               //$('.slick-arrow').hide();
				// display the carousel
				that.display();
			});

			// bind the play button click event
			$body.on(
				'mouseover',
				'.m1-hero__carousel',
				function () {
					that.$M1_slider.slick('pause');
					$('.slick-arrow').show();
					
			});

			// bind the play button click event
			$body.on(
				'mouseover',
				'.slick-arrow',
				function () {
					that.$M1_slider.slick('pause');
					$('.slick-arrow').show();
					
			});

			// bind the pause button click event
			$body.on(
				'mouseleave',
				'.m1-hero__carousel',
				function () {
					that.$M1_slider.slick('play');
					$('.slick-arrow').hide();					
			});

			// initialise the carousel
			this.$M1_slider.slick({
				arrows: true,
				dots: true,
				infinite: true,
				speed: 750,
				slidesToShow: 1,
				slidesToScroll: 1,
				autoplay: true,
				autoplaySpeed: 3000,
				pauseOnHover: true,
				adaptiveHeight: true,
				nextArrow: '.m1-hero__carousel-button--next',
				prevArrow: '.m1-hero__carousel-button--prev',
				rtl: ($('html').attr('dir') == 'rtl')
			});
		} else {
			// only one image, do not initialise the carousel, just display the image
			this.display();
		}
	};

	M1_HERO.prototype.display = function () {
		// set image as background for out of bounds scaling
		$('.m1-hero__carousel-slide-image, .m1-hero__carousel-slide-gradient').each(function () {
			var target = $(this);
			target.css({
				backgroundImage: "url('"+$(this).data('src')+"')"
			})
			if (utils.isLessThanIE9()) {
				target.css('filter', 'progid:DXImageTransform.Microsoft.AlphaImageLoader(src=\''+$(this).data('src')+'\', sizingMethod=\'scale\')');
				target.css('-ms-filter', 'progid:DXImageTransform.Microsoft.AlphaImageLoader(src=\''+$(this).data('src')+'\', sizingMethod=\'scale\')');
			}
		});

		$('.m1-hero__carousel-slide-gradient').each(function(){
			var target = $(this);
			target.css({
				background: "rgba(0, 0, 0, 0.4)"
			})
		});

		this.$M1_slider.css({
			top: 0,
			opacity: 1,
			position: 'relative'
		});

		var index = ""

		
	}

	return M1_HERO;
});

