define('../src/sublayouts/m5-quick-services/m5-quick-services',['jquery', 'select2'], function($, select2) {

    'use strict';

    var M5_qServices = function($component) {

        this.$component = $component;

        return this;
    };

    /**
     * Initialise component
     */

    M5_qServices.prototype.init = function() {
        var _this = this,
            upload_number = 2;

        //this.emailPattern = new RegExp(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

        $('.m5-quick-services--item').each(function() {
            _this.bindModal($(this).attr('modal_type'));
        });
        //this.bindModal('mail');
        //this.bindModal('event');
        //this.allowEmail($('.m5-quick-services--mail_select'));
        //this.allowEmail($('.m5-quick-services--event_select'));

        $('.m5-quick-services--upload_first').on('change', function() {
            $('.m5-quick-services--upload_more').css('display', 'inline-block');
        });

        $('.m5-quick-services--upload_more').on('click', function() {
            var d = document.createElement("div");
            var file = document.createElement("input");
            file.setAttribute("type", "file");
            file.setAttribute("name", "attachment" + upload_number);
            d.appendChild(file);
            document.getElementById("moreUploads").appendChild(d);
            upload_number++;
        });

    };

    M5_qServices.prototype.bindModal = function(type) {
        var _this = this,
            $parent = $('.m5-quick-services--' + type + '_modal'),
            $trigger = $parent.find('.m39-modal__trigger'),
            $close = $parent.find('.m39-modal__button--close');

        $('.m5-quick-services--item_' + type).off('click.modal').on('click.modal', function() {
            var __this = this;

            if (type == 'event') {
                $parent.find('.m39-modal__title').html($(__this).html());
                $parent.find('#form-field-requesttype').find('option').each(function () {
                    if ($(this).val() == $(__this).attr('event_type')) {
                        $(this).prop('selected', true);
                    };
                });
            };

            $trigger.trigger('click');
        });

        $('.m5-quick-services--' + type + '_cancel').off('click.modal').on('click.modal', function() {
            $close.trigger('click');
        });

        $parent.find('.m39-modal__button--close').off('click.close').on('click.close', function() {
            $parent.find('.select2-selection__rendered').find('.select2-selection__choice').each(function() {
                $(this).remove();
            });
            $parent.find('.form-field__input').not('select').each(function() {
                $(this).parsley().reset();
                $(this).val('');
                $(this).removeClass('form-field__input--error').removeClass('form-field__input--validated');
                $(this).closest('span').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
            });
            //$parent.find('select').each(function() {
                //if ($(this).hasClass('form-field__input--select2')) {
                    //$(this).val(null).trigger("change");
                //};
                //$(this).parsley().reset();
                //$(this).closest('span').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
            //});
        });
    };

    M5_qServices.prototype.allowEmail = function($el) {
        var _this = this;

        $el.select2({
            tags: true,
            createTag: function(params) {
                if (!(_this.emailPattern.test(params.term))) {
                    return null
                };
                return {
                    id: params.term,
                    text: params.term
                }
            },
            placeholder: $(this).attr('placeholder')
        });
    };

    return M5_qServices;
});

